<template>
  <div class="page">
    <div class="t-gray t-sm" style='padding:.5rem;'></div>
    <div class="order-info ui-list">
        <div class='ui-list-item' flex>
          <div class='ui-list-label' flex-box="0"></div>
          <div flex-box="1">{{product.name}}</div>
        </div>
        <div class='ui-list-item' flex>
          <div class='ui-list-label' flex-box="0"></div>
          <div flex-box="1">
            <span class="t-orange">{{price}}</span>
          </div>
        </div>
        <div class='ui-list-item' flex>
          <div class='ui-list-label' flex-box="0"></div>
          <div flex-box="1">
            <mt-button plain size="small" @click.native="doRefreshPrice"></mt-button>
          </div>
        </div>
    </div>
    <div class="t-gray t-sm" style='padding:.5rem;'></div>
    <div style='padding:1rem;background:#fff;'>
      <div class='mb-20'>
        <a v-if='payResult.url' target='_blank' :href='payResult.url' class="btn-pay mint-button mint-button--danger mint-button--large"></a>
      </div>
      <div class="t-center">
        <img src="@/assets/20180316111851.png" style='width:100%;'/>
      </div>
    </div>
    <mt-popup
      v-model="popupFail"
      popup-transition="popup-fade">
      <div class="ui-alert">
        <div class="ui-alert-title"></div>
        <div class="ui-alert-body">
          <div flex>
            <div flex-box="0">
              <i style='font-size:2rem;margin-right:1rem;' class="iconfont icon-tishi t-red"></i>
            </div>
            <div flex-box="1">
              <div class="mb-10">,</div>
              <div class="mb-20">:</div>
            </div>
          </div>
          <div flex class="mb-10">
            <div flex-box="1" style='margin-right:.5rem;'>
              <mt-button type="danger" style='width:100%;'></mt-button>
            </div>
            <div flex-box="1">
              <mt-button type="default" style='width:100%;'></mt-button>
            </div>
          </div>
          
          <div class="t-center t-xs">
              :010-5165777
          </div>
        </div>
      </div>
    </mt-popup>
  </div>
</template>

<script>
import { mapActions } from "vuex";

export default {
  data() {
    return {
      popupFail: false,
      popupSuccess: true,
      popupPaying: true,
      product: {},
      orderId: "",
      price: 0,
      productId: "",
      payResult: {}
    };
  },
  methods: {
    ...mapActions({
      getPayInfo: "getPayInfo",
      refreshPrice: "refreshPrice",
      pay: "pay"
    }),
    doRefreshPrice() {
      this.refreshPrice(this.orderId).then(({ code, data, msg }) => {
        this.$toast(msg);
        if (code == 200) {
          this.price = data;
          this.getQRcode();
        }
      });
    },
    getQRcode() {
      this.pay({
        product_id: this.productId,
        channel: "ums"
      }).then(data => {
        console.log(data);
        this.payResult = data;
      });
    }
  },
  mounted() {
    let { id: product_id } = this.$route.query;
    this.productId = product_id;
    this.getPayInfo({ product_id }).then(
      ({ code, msg, price, token, product }) => {
        this.price = price;
        this.orderId = token;
        this.product = product;
        this.getQRcode();
      }
    );
  }
};
</script>
<style lang="less" scoped>
.page {
  padding: 40px 0 50px;
  height: 100vh;
  box-sizing: border-box;
}
.order-info {
  background: #fff;
  padding: 0.5rem;
}

.ui-list-item {
  margin-bottom: 0.5rem;
  .ui-list-label {
    color: #666;
    width: 3.2rem;
  }
}
.btn-pay{
  line-height: 41px;
  text-decoration:none;
}
</style>


